
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
body{
  color: #e65d6e;
}

#base {
  width: 100%;
  position: relative;
}
.side {
  width: 420px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 22;
  background: #42b983;
  left: 0px;
}
.content-outter {
  width: 100%;
  height: 100%;
  position: fixed;
}
.content {
  padding-left: 420px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  min-height: 100vh;
  padding-bottom: 40px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.lang-change {
  width: 100%;
  text-align: right;
  position: fixed;
  top: 40px;
  right: 32px;
}
.lang-change .flag {
  width: 20px;
  vertical-align: middle;
  margin-right: 14px;
}
.lang-change .lang-item {
  height: 34px;
  line-height: 34px;
  padding: 0 8px;
  cursor: pointer;
}
.lang-change .lang-item:hover {
  background: #EDF1F8;
  border-radius: 3px;
}
.copyright {
  position: fixed;
  left: 420px;
  bottom: 22px;
  color: #002059;
  opacity: 0.2;
  text-align: center;
  width: calc(100% - 420px);
}
@media screen and (max-width: 1000px) {
.side {
    display: none;
}
.content {
    padding-left: 0;
    width: 100%;
}
.copyright {
    left: 0;
    width: 100%;
}
}
